@charset "UTF-8";

/* 0 - 全局
-------------------------------- */

a{ color: #c40b00; }

.wrap{ max-width: 1000px; }

/* 1 - 页眉
-------------------------------- */

/* - 侧边栏 */
.sidebar{
    top: 0;
    left: 0;
    bottom: 0;
    color: #fff;
    width: 250px;
    position: fixed;
    overflow: hidden;
    overflow-y: auto;
    background: #4b4b4b;
}

/* -- 头像 */
.sidebar .avatar{
    width: 160px;
    height: 160px;
    margin: 2em auto;
    background: #fff;
    border-radius: 100%;
    border: #fff 5px solid;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    -o-transition: transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}
.sidebar .avatar:hover{
    -webkit-transform: rotate(1turn);
        -ms-transform: rotate(1turn);
            transform: rotate(1turn);
}
.sidebar .avatar img{
    border-radius: 100%;
}

/* -- 侧边栏导航 */
.sidebar .nav{
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    list-style: none;
    -ms-flex-direction: column;
        flex-direction: column;
}
.sidebar .nav a{
    color: #fff;
    padding: 1em;
    display: block;
    text-align: center;
    -webkit-transition: background .3s, -webkit-box-shadow .3s;
    transition: background .3s, -webkit-box-shadow .3s;
    -o-transition: background .3s, box-shadow .3s;
    transition: background .3s, box-shadow .3s;
    transition: background .3s, box-shadow .3s, -webkit-box-shadow .3s;
}
.sidebar .nav a:hover{
    background: rgba(0, 0, 0, .2);
}

main{
    margin-left: 250px;
}

@media screen and (max-width: 900px) {
    .sidebar {
        width: inherit;
        position: relative;
    }
    .sidebar .nav{
        background: #f8bd24;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-pack: center;
            justify-content: center;
    }
    .sidebar .nav a:hover{
        background: none;
        -webkit-box-shadow: 0 -3px 0 #fff inset;
                box-shadow: 0 -3px 0 #fff inset;
    }
    main{
        margin-left: 0;
    }
}

@media print{
    .sidebar{
        display: none;
    }
    main{
        margin-left: 0;
    }
}


/* 2 - 正文
-------------------------------- */
main section{
    padding: 5em 0;
    position: relative;
}
@media screen and (max-width: 900px) {
    main section{
        padding: 3em 0;
    }
}

#skills{ background: #f8bd24; }

.title{
    margin-bottom: 1em;
}
.title:before{
    float: left;
    margin-right: .5em;
    font-family: "FontAwesome";
}

#info .title:before{
    content: "\f005";
}
#skills .title:before{
    content: "\f19d";
}
#works .title:before{
    content: "\f091";
}
#story .title:before{
    content: "\f0b1";
}
#team .title:before{
    content: "\f024";
}

/* - 技能 */
.skills-icon{
    width: 100px;
    height: 100px;
    margin: 0 auto;
    background: #fff;
    border-radius: 100%;
    -webkit-box-shadow: 0 5px rgba(0,0,0,0.3);
            box-shadow: 0 5px rgba(0,0,0,0.3);
}
.skills-icon:hover i{
    -webkit-transform: rotate(1turn);
        -ms-transform: rotate(1turn);
            transform: rotate(1turn);
}
.skills-icon i{
    font-size: 50px;
    line-height: 100px;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    -o-transition: transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}
.skills-title{
    margin-top: 1.5em;
    text-align: center;
}

/* - 作品 */
.works-item img{
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    -o-transition: transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}
.works-item img:hover{
    -webkit-transform: scale(1.05, 1.05);
        -ms-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);
}
.works-item p{
    padding: 1em 0;
    text-align: center;
}

/* - 经历 */
.timeline{
    list-style: none;
    margin-left: 1em;
    padding-left: 1.5em;
    border-left: 2px grey solid;
}

.timeline li{
    line-height: 1.5em;
    position: relative;
    margin-bottom: 1.5em;
}
.timeline li p{
    font-size: 0.8rem;
}
.timeline li:hover:before{
    -webkit-transform: scale(1.2, 1.2);
        -ms-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
}
.timeline li:before{
    content: "";
    left: -2.3em;
    width: 1.5em;
    height: 1.5em;
    display: block;
    position: absolute;
    background: #f8bd24;
    border-radius: 100%;
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    -o-transition: transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}


/* 3 - 页尾
-------------------------------- */
footer{
    color: #fff;
    padding: 1em 0;
    text-align: center;
    background: #72655b;
}
footer a{
    color: inherit;
}


